html, body, #root {
  height: 125%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  background: linear-gradient(180deg, rgba(212,51,210,1) 0%, rgba(212,117,51,1) 19%, rgba(64,145,199,1) 22%, rgba(18,11,96,1) 80%);
}

@font-face {
  font-family: TitleFont;
  src: url(https://pierre605.github.io/Battleship-ReactJS-/pixie_font.ttf);
}

h2 {
  font-family: TitleFont;
  font-size: 1.9rem;
}

#sound-icon {
  cursor: pointer;
  width: 4%;
  float: right;
  margin-top: -37px;
  margin-right: 2%;
}

#ships-pics {
  margin-top: 4em;
  position: relative;
}

#ships-group {
  display: flex;
  justify-content: space-between;
}

#subs-group {
  display: flex;
  justify-content: space-around;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#opp-grid {
  display: none;
}

.title {
  display: flex;
  justify-content: center;
  margin: auto;
  padding: 1em;
}

#grids {
  display: flex;
  justify-content: space-around;

}

/* #center {
  width: fit-content;
} */

#vs {
  display: none;
  width: 15em;
  height: 15em;
  margin-top: 2em;
  margin-right: -7em;
  margin-left: -7em;
  }

#info-wrap-your-grid {
  font-size: medium;
  display: flex;
  flex-direction: row-reverse;
  position: absolute;
}

#info-wrap-algo-grid {
  font-size: medium;
  display: flex;
  position: absolute;
}

.flex {
  display: flex;
  justify-content: space-evenly;
}

/* End Game Modal */
/* The Modal (background) */
.modal {
	display: none; /* Hidden by default */
	position: fixed; /* Stay in place */
	z-index: 1; /* Sit on top */
	padding-top: 100px; /* Location of the box */
	left: 0;
	top: 0;
	width: 100%; /* Full width */
	height: 100%; /* Full height */
	overflow: auto; /* Enable scroll if needed */
	background-color: rgb(0,0,0); /* Fallback color */
	background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  }
  
/* Modal Content */
.modal-content {
  background-color: whitesmoke;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 28%;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

p {
  font-size: 0.75em;
}

.score {
  font-size: 10px;
  width: fit-content;
  text-align: center;
}
  
/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

#vic-pic {
  width: 35%;
}
#loose-pic {
  width: 65%;
}


/* Responsive */
@media screen and (min-width: 555px) and (max-width: 825px) {
  h2 {
    font-size: larger;
  }

  #sound-icon {
    width: 5%;
  }

  #vs {
    display: none;
    width: 14em;
    height: 14em;
    margin-right: -7em;
    margin-left: -7em;
    margin-top: 2em;
    }
    
  #info-wrap-your-grid {
    font-size: small;
  }
  
  #info-wrap-algo-grid {
    font-size: small;
  }

  .modal-content {
    width: 28%;
    }
    
  p {
    font-size: 0.75em;
  }

  .score {
    font-size: 10px;
  }
}

@media screen and (min-width: 2px) and (max-width: 555px) {
  html, body, #root {
    height: 100%;
  }

  h2 {
  font-size: 15px;
  }

  #sound-icon {
    width: 6%;
  }

  #grids {
    flex-direction: column-reverse;
    align-items: center;
  }

  #vs {
    display: none;
    width: 5em;
    height: 4em;
    margin-top: auto;
    margin-right: 11em;
    }

  #center {
    display: block;
    height: 1.5em;
  }
  
  #your-grid {
    display: flex;
    flex-direction: column-reverse;
  }
    
  #info-wrap-your-grid {
    font-size: x-small;
    height: 14px;
    position: static;
  }
  
  #info-wrap-algo-grid {
    font-size: x-small;
  }

  #ships-pics {
    margin-top: 3em;
    position: relative;
  }

  .modal-content {
    width: 21%;
    }
    
  p {
    font-size: 0.6em;
  }

  #loose-pic {
    width: 100%
  }

  .score {
    font-size: 8px;
  }
}